<template lang="html">
  <el-dialog :visible.sync="dialogVisible" title="" width="400px">
    <div>
      <span class="highlight-text"> </span>:
      <span class="highlight-text">{{ data.gameName }}</span>&nbsp;&nbsp;&nbsp; 
      <span class="highlight-text">{{ data.drawno }} ?</span>
    </div>
    <span slot="footer">
      <el-button type="primary" @click="handleConfirm"></el-button>
      <el-button @click="dialogVisible = false"></el-button>
    </span>
  </el-dialog>
</template>

<script>
import config from '@/config/data'

export default {
  name: 'LotteryResultsDialogCancel',
  props: {
    data: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      dialogVisible: false
    }
  },
  methods: {
    // 
    handleConfirm () {
      this.dialogVisible = false // 

      this.$httpAPI.updateLotteryBillCancel({
        gameType: this.data.gameType,
        drawno: this.data.drawno
      }).then((response) => {
        if (response.data.status === 200) {
          this.$emit('on-canceled')
          this.$message.success(config.OPERATION_SUCCEEDED)
        } else {
          this.$message.error(`${config.OPERATION_FAILED}: ${response.data.msg}`)
        }
      }).catch((error) => {
        console.log(error)
        this.$message.error(config.SERVER_RESPONSE_EXCEPTION)
      })
    },
    // 
    toggleDialogVisible (status) {
      this.dialogVisible = status
    }
  }
}
</script>

<style scoped>
.highlight-text {
  color: #F56C6C;
}
</style>
